.wrapper {
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
}

.hat {
    flex: 0;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #554590;
}

.logoImage {
    margin-top: -10px;
}

.main {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: calc(100vh - 80px);
    padding: 1rem 0 100px 0;
    flex: 1 0 auto;
}

.barContainer {
    position: relative;
    display: flex;
    justify-content: space-between;
}

.barLine {
    position: absolute;
    background-color: #EAEEF1;
    height: 4px;
    margin-top: -2px;
    z-index: 0;
}

.barUnitContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
}

.barUnitText {
    font-size: 0.8rem;
}

.barUnitRound {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #EAEEF1;
    border-radius: 100%;
}

.barUnitRound.complete {
    /*background-color: red;*/
}

.barUnitRound.current {
    border: 1.5px solid #38B54A;
    box-sizing: border-box;
}

.check {
    border: 0 solid #38B54A;
    border-left-width: 3px;
    border-bottom-width: 3px;
    transform: rotate(-48deg);
}

.input {
    width: 100%;
    padding-left: 1.5rem;
    font-size: 0.9rem;
    border: 1px solid #eeeeee;
    background-color: #F8F8F8;
    color: #5C5C5F;
    margin-bottom: 0.7rem;
}

.buttonContainer {
    width: 20rem;
}

.button {
    margin-top: 0.5rem;
}

.areaHeader {
    padding-left: 1rem;
}

.areaCompany {
    padding-left: 1.5rem;
    padding-top: 0.8rem;
    border-radius: 0.2rem;
    margin-bottom: 2rem;
    border: 1px solid #eeeeee;
    background-color: #F8F8F8;
    width: 100%;
    height: 100px;
    resize: none;
    outline: none;
    font-size: 0.9rem;
}

.areaBusiness {
    padding-left: 1rem;
    font-size: 0.9rem;
    padding-top: 0.6rem;
    border-radius: 0.2rem;
    margin-bottom: 1rem;
    border: 1px solid #dedede;
    width: 100%;
    height: 130px;
    resize: none;
    outline: none;
}

.areaHeader {
    font-weight: 700;
    font-size: 0.9rem;
    margin-bottom: 3px;
    text-align: left;
    padding-left: 1rem;
}

.header {
    justify-content: center;
    font-weight: 700;
    font-size: 1.3rem;
    margin-bottom: 1rem;
}

.subheader {
    justify-content: center;
    font-size: 1.1rem;
    font-weight: 700;
}

.footer {
    position: absolute;
    bottom: 0;
    margin-top: 3rem;
}